#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.to-do-item {
  text-transform: capitalize;
  border: 1px solid black;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
  gap: 8px;
  margin-bottom: 10px;
}
.page-ignitions-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
}

.page-val {
  cursor: pointer;
}
.page-val.active {
  font-weight: bold;
  color: blue;
  font-size: 20px;
}
.page-val-divider {
  margin: 4px;
}
.page-ig-nav {
  cursor: pointer;
  border: 1px solid black;
  padding: 0 4px;
}
